@import url(https://fonts.googleapis.com/css2?family=Epilogue:ital@1&family=Roboto:wght@100&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background: #17202A ;
  font-family: 'Epilogue', sans-serif;
}

.react-stockchart{
    margin-bottom: 1rem;
    background-color: #051320;
}
.stock__table{
  width: 100%;
  border: 3px solid white;
  border-radius: 10px;
  /* padding: 1rem; */
  margin-top: 2rem;
}

table, th, td{
  width: 100%;
  border: 1px solid white;
  border-collapse: collapse;
 

}
th {
  color: gray;
  padding: 3px;
  text-align: center;
  width: 35%;

}

td{
    color: white;
    padding: 15px;
    text-align: center;
}

tr:nth-child(even) {background-color: black;}
:root{
    --meta_info-bg: #051320;
}

.meta__info{
    width: 100%;
    border: 3px solid #D8DDE2;
    border-radius: 10px;
    padding: 2rem;
    color: #D8DDE2;
    font-size: 1vw;
    text-align: right;
    background-color: #051320;
    background-color: var(--meta_info-bg);
}

.meta__info h1, h3, h4 {
    background-color: #051320;
    background-color: var(--meta_info-bg);

}

.stock__meta__data{
    color: gray;
    float: left;
    background-color: #051320;
    background-color: var(--meta_info-bg);
}

:root{
    --purchase-bg:#051320;
}


.purchase{
    color: white;
    font-size: 100%;
    background-color: #051320;
    background-color: var(--purchase-bg);
}

#trans_buttons{
    padding: 5%;
    /* margin-top: 3%; */
    margin-left: 5%;
    display: flex;
    color: white;
    width: 90%;
    font-size: 125%;
    background-color: #051320;
    background-color: var(--purchase-bg);
}

.inner{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.inner h3 {
    margin: 0.5rem auto;
    background-color: #051320;
    background-color: var(--purchase-bg);
}

.btn-primary{
    margin-right: 1rem;
    background-color: black;
}


.radio_btn {
    margin-right: 25%;
    font-size: 100%;
    background-color: #051320;
    background-color: var(--purchase-bg);
}

.purchase input{
    text-align: center;
    width: 50%;
    margin: auto;
    font-size: 125%;
}
.purchase label{
    font-size: 125%;
    margin-left: 39%;
    background-color: #051320;
    background-color: var(--purchase-bg);
    justify-content: center;
}

.alerts{
    width: 90%;
    /* height: 3.5rem; */
    margin: 1rem auto;
    background-color: #051320;
    background-color: var(--purchase-bg);
}

.alert{
    text-align: center;
    margin: auto;
    border-radius: 10px;
    height: 100%;
    width: 95%;
}

.select{
    width: 50%;
    margin: auto;
    margin-top: 5%;
}
.select h3 {
    font-size: 125%;
    text-align: center;
}

.btn{
    margin-left: 25%;
    margin-top: 3rem;
    margin-bottom: 2rem;
    padding: 1rem;
    width: 50%;
    background-color: #17202A;
    
}

.sub_btn{
    background-color: #051320;
    background-color: var(--purchase-bg);
}

:root{
    --purchase-bg:#051320;
    --meta_info-bg: #051320;
}


.stock__elements{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-evenly;
    margin: 0rem auto;
    position: relative;
}

.purchase__container{
    border: 5px solid #6F767D;
    background-color: #051320;
    background-color: var(--purchase-bg);
    width: 45%;
    border-radius: 10px;
    float: right;
}

.purchase__container form {
    background-color: #051320;
    background-color: var(--purchase-bg);
}

.stock__graph{
    border: 5px solid #6F767D;
    border-radius: 15px;
    margin: 1rem;
    background-color: #051320;
    background-color: var(--meta_info-bg);

}

.meta_info{
    border: 5px solid #6F767D;
    border-radius: 15px;
    padding: 1rem;
    width: 50%;
    height: 41rem;
    float: left;
}

.stock__table2{
    margin-top: 2rem;
}

.stock__table2 td, th{
    background-color: #051320;
    background-color: var(--meta_info-bg);
}

.all_stock__info{
    justify-content: space-evenly;
    width: 500%;
    padding: 1rem;
    margin: 1rem;
}





.home_page{
    border-radius: 10px;
    margin: 1rem;
    padding: 1rem;
    

}

.stock__search{
    margin-left: 5rem;
    width: 90%;
    color: #fff;

}
.stock__search input[type=text]{
    width: 100%;
    margin-bottom: 0.5rem;
    background-color: #ffffff;
    border-radius: 10px;
    font-size: 2rem;
    text-align: center;
}

.stock__search h5{
    text-align: center;
}

.stock__graph{
    width: 100%;
}
.auth__container{
    border: 3px solid white;
    border-radius: 10px;
    float: center;
    width: 35%;
    /* height: 30rem; */
    margin: 10rem auto;
    padding: 2rem;
    color: white;
}


.auth__container h2{
    text-align: center;

}

.switch a {
    text-decoration: none;
    background-color: transparent;
    color: blue;
    margin-left: 0;
}

.switch{
    margin: 2rem auto;
    text-align: center;
}
.switch h3 a {
    font-size: 1.5rem;
    
}
.sign__up{
    border: 5px solid white;
    border-radius: 10px;
    float: center;
    width: 35%;
    margin: 10rem auto;
    padding: 2rem;
    color: white;
}

.sign__up h2{
    text-align: center;
    font-size: 2rem;
}


.sign__up a{
    text-decoration: none;
    background-color: transparent;
    color: blue;
    margin-left: 0;
}

.switch{
    margin: 0.5rem auto;
    text-align: center;
}
.switch h3,a {
    font-size: 1.5rem;
    background-color:  #17202A;
}
.stock__card{
    border: 5px solid white;
    border-radius: 15px;
    padding: 2rem;
    margin: 2rem auto;
    justify-content: center;
    width: 75%;
    font-size: 1.3rem;
    background-color: #051320;
}

.stock__card ul {
    color: white;
    text-align: center;
    list-style: none;
}

.not_logged{
    border: 5px solid white;
    border-radius: 15px;
    padding: 2rem;
    margin: 2rem auto;
    justify-content: center;
    width: 75%;
    font-size: 1.3rem;
    background-color: #051320;
}

.not_logged ul{
    color: white;
    text-align: center;
    list-style: none;
    background-color: #051320; 

}

.not_logged a {
    background-color: transparent;
    color: blue;
}

.not_logged a:hover {
    background-color: transparent;
}

.not_logged li {
    font-size: 2rem;
    margin-top: 2rem;
    background-color: #051320; 
}


.stock__card li {
    text-decoration: none;
    text-align: right;
    background-color: #051320;
}

.stock__card .data{
    background-color: #051320;
}

.stock__card .meta{
    color: grey;
    float: left;
    background-color: #051320;
}




.portfolio__{
    display: flex;
    flex-direction: row-reverse;
}


.header_stats{
    border: 5px solid white;
    padding: 2rem;
    border-radius: 15px;
    width: 40%;
    margin: auto;
    background-color: #051320;
    }


.header_stats ul {
    color: white;
    text-align: center;
    list-style: none;
    background-color: #051320;

}

.header_stats li {
    text-decoration: none;
    text-align: left;
    background-color: #051320;
    margin-top: 1rem;
    font-size: 2rem;
}

.header_stats .meta {
    background-color: #051320;
    color: gray;
}




.header_stats .data{
    float: right;   
    background-color: #051320;

}

.header_stats .btn{
    width: 75%;
    margin-left: 12.8%;
}

.header_stats .reset_btn{
    background-color: #051320;
    justify-content: center;
}


.stocks__{
    border: 5px solid white;
    border-radius: 15px;
    width: 50%;
    margin: auto;

}


