    text-decoration: none;
    color: inherit;
    background-color: #343A40;
 }
 .lg_f{
     background-color: #343A40;
 }
 .lg_f:hover{
     color: #FFF;
     cursor: default;
 }

 .navbar-nav{
     background-color: #343A40;
 }

 .navbar-brand{
     padding: 1rem;
     background-color: #343A40;
 }


 .ttle{
     padding: 1rem;
     background-color: #343A40;
     font-size: 2.2rem;
 }
 #responsive-navbar-nav{
    background-color: #343A40;

 }

 .nav-link{
     font-size: 1.9rem;
